import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    // 路由独享前置守卫 和全局前置守卫一样
    // beforeEnter: (to, from, next) => {
    //   next()
    // },
    // 路由元信息
    // meta: { auth: true },
    // children: [
    //   {
    //     path: 'list',
    //     component: () => import('../views/About.vue'),
    //     name: 'list',
    //     meta: { auth: true }
    //   }
    // ]
  },
  {
    path: '/about',
    name: 'About',
    // 路由懒加载
    // About 被访问的时候才导入
    component: () => import('../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to,from,savePosition){
    if(savePosition){
      return savePosition
    }else {
      return {x: 0, y: 0}
    }
  }
})

